import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Pie } from 'react-chartjs-2';
import { Chart, ArcElement, Tooltip, Legend } from 'chart.js';
import HalvingCountdown from '../halvingcountdown';

Chart.register(ArcElement, Tooltip, Legend);

function Tokenomics() {
  useEffect(() => {
    document.title = "Token Website | $LVN Token | Tokenomics";
  }, []);

  return (
    <div className="main-layout page-content tokenomics-page">
      {/* Countdown Timer */}
      <div style={{background: 'rgba(175, 0, 255, 0.05)', padding: '0.75rem', borderRadius: '8px', marginBottom: '2rem', border: '1px solid var(--color-purple)'}}>
        <HalvingCountdown />
      </div>

      {/* MAIN CONTENT */}
      <main className="page-content">
        {/* ===== HEADER ===== */}
        <div className="header-wrapper">
          <h1 className="header-title">
            <span className="header-symbol text-purple">$</span>
            <span className="text-teal">LVN</span>
          </h1>
          <span className="header-tagline text-purple">Sustainable Tokenomics</span>
        </div>
        
        {/* ===== KEY METRICS ===== */}
        <div style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'space-around', marginBottom: '2rem'}}>
          <div style={{textAlign: 'center', margin: '1rem', flex: '1 1 200px'}}>
            <div style={{fontSize: '2.5rem', fontWeight: 'bold', color: 'var(--color-teal)'}}>8,008,135</div>
            <div style={{color: 'var(--color-purple)'}}>Total Supply</div>
          </div>
          <div style={{textAlign: 'center', margin: '1rem', flex: '1 1 200px'}}>
            <div style={{fontSize: '2.5rem', fontWeight: 'bold', color: 'var(--color-teal)'}}>4 Years</div>
            <div style={{color: 'var(--color-purple)'}}>Halving Cycle</div>
          </div>
          <div style={{textAlign: 'center', margin: '1rem', flex: '1 1 200px'}}>
            <div style={{fontSize: '2.5rem', fontWeight: 'bold', color: 'var(--color-teal)'}}>0%</div>
            <div style={{color: 'var(--color-purple)'}}>Added Inflation</div>
          </div>
        </div>
        
        {/* ===== TOKENOMICS EXPLANATION ===== */}
        <div className="paragraph-container">
          <h2 style={{color: 'var(--color-teal)', marginBottom: '1rem'}}>The Bitcoin-Inspired Model</h2>
          <p>
            <span className="text-teal">$</span><span className="text-purple">LVN</span>'s tokenomics are built on the proven Bitcoin halving model, where token release is cut in half every four years. This creates natural scarcity and rewards early believers while allowing fair distribution over time.
          </p>
          <p>
            Unlike tokens with hidden inflation or unpredictable supply changes, <span className="text-teal">$</span><span className="text-purple">LVN</span>'s release schedule is hardcoded into our smart contracts and cannot be altered. This predictability allows for long-term planning and sustainable growth.
          </p>
        </div>
        
        {/* ===== TOKEN ALLOCATION CHART ===== */}
        <h2 style={{color: 'var(--color-teal)', textAlign: 'center', margin: '2rem 0 1rem'}}>Token Allocation</h2>
        <div style={{flex: 1, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', minHeight: 0, margin: '1rem 0 2.5rem'}}>
          <div className="tokenomics-chart-container" style={{width: '100%', maxWidth: 400, minWidth: 250, height: 'auto', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
            <PieChartLVN />
          </div>
        </div>
        
        {/* ===== ALLOCATION BREAKDOWN ===== */}
        <div style={{margin: '0.5rem 0 3rem'}}>
          <h3 style={{color: 'var(--color-teal)', marginBottom: '1.5rem', textAlign: 'center'}}>Allocation Breakdown</h3>
          
          <div style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'center', gap: '1rem'}}>
            <AllocationBox 
              color="#8e44ad" 
              title="Halving Wallet (Locked)" 
              amount="4,317,601" 
              percentage="53.9%" 
              description="Released over decades following the Bitcoin halving model, creating predictable scarcity" 
            />
            
            <AllocationBox 
              color="#1abc9c" 
              title="Liquidity Pool" 
              amount="1,000,000" 
              percentage="12.5%" 
              description="Ensures market liquidity and trading depth on decentralized exchanges" 
            />
            
            <AllocationBox 
              color="#3498db" 
              title="Staking Rewards" 
              amount="1,000,000" 
              percentage="12.5%" 
              description="Dedicated to rewarding long-term holders through staking programs" 
            />
            
            <AllocationBox 
              color="#f1c40f" 
              title="Airdrop (Locked)" 
              amount="900,000" 
              percentage="11.2%" 
              description="Community distribution following a decay model to ensure fair, long-term allocation" 
            />
            
            <AllocationBox 
              color="#e67e22" 
              title="Development" 
              amount="418,001" 
              percentage="5.2%" 
              description="Funds future development, technical improvements, and security audits" 
            />
            
            <AllocationBox 
              color="#e74c3c" 
              title="Treasury" 
              amount="272,533" 
              percentage="3.4%" 
              description="Reserved for strategic partnerships, future initiatives, and community grants" 
            />
            
            <AllocationBox 
              color="#2ecc71" 
              title="Airdrop (Unlocked)" 
              amount="100,000" 
              percentage="1.2%" 
              description="Initial community distribution to early supporters and adopters" 
            />
          </div>
        </div>
        
        {/* ===== WHY THIS MODEL WORKS ===== */}
        <div className="paragraph-container" style={{marginTop: '1rem', background: 'rgba(108, 255, 230, 0.05)', padding: '1.5rem', borderRadius: '8px'}}>
          <h2 style={{color: 'var(--color-teal)', marginBottom: '1rem', textAlign: 'center'}}>Why This Model Works</h2>
          <p>
            Bitcoin's halving model has proven itself over 15+ years, creating the most successful digital asset in history. By adopting this time-tested approach, <span className="text-teal">$</span><span className="text-purple">LVN</span> benefits from:
          </p>
          <ul style={{listStyle: 'disc', paddingLeft: '2rem', margin: '1rem 0'}}>
            <li><span className="text-purple">Natural price discovery</span> — Market finds value based on predictable supply</li>
            <li><span className="text-teal">Alignment of incentives</span> — Early adopters and long-term holders are rewarded</li>
            <li><span className="text-purple">Sustainable distribution</span> — No artificial pumps followed by dumps</li>
            <li><span className="text-teal">Community-centric growth</span> — Value builds through genuine adoption over time</li>
          </ul>
        </div>
        
        {/* ===== BACK LINK ===== */}
        <div className="back-link-container" style={{marginTop: '2.5rem', marginBottom: '3.5rem'}}>
          <Link to="/" className="back-link">← Back to Home</Link>
        </div>

        <div className="bottom-divider"></div>
      </main>
    </div>
  );
}

// Allocation box component
function AllocationBox({ color, title, amount, percentage, description }) {
  return (
    <div style={{
      width: '280px',
      padding: '1rem',
      margin: '0.5rem',
      borderRadius: '8px',
      background: 'rgba(0, 0, 0, 0.2)',
      borderLeft: `4px solid ${color}`
    }}>
      <div style={{display: 'flex', alignItems: 'center', marginBottom: '0.5rem'}}>
        <div style={{width: '12px', height: '12px', borderRadius: '50%', background: color, marginRight: '0.5rem'}}></div>
        <h4 style={{color: 'var(--color-teal)', margin: 0}}>{title}</h4>
      </div>
      <div style={{fontSize: '1.1rem', fontWeight: 'bold', color: 'var(--color-purple)'}}>{amount} <span style={{fontSize: '0.9rem', opacity: 0.8}}>({percentage})</span></div>
      <p style={{fontSize: '0.9rem', marginTop: '0.5rem', opacity: 0.9}}>{description}</p>
    </div>
  );
}

function PieChartLVN() {
  // Pie chart data for $LVN tokenomics
  const data = {
    labels: [
      'Halving Wallet (Locked)',
      'Liquidity Pool Wallet',
      'Staking Pool Wallet',
      'Airdrop Wallet (Locked)',
      'Developer Wallet',
      'Treasury Wallet',
      'Airdrop Wallet (Unlocked)',
      'Halving Wallet (Unlocked)'
    ],
    datasets: [
      {
        data: [4317601, 1000000, 1000000, 900000, 418001, 272533, 100000, 0],
        backgroundColor: [
          '#8e44ad',
          '#1abc9c',
          '#3498db',
          '#f1c40f',
          '#e67e22',
          '#e74c3c',
          '#2ecc71',
          '#bdc3c7'
        ],
        borderWidth: 1,
      },
    ],
  };
  
  const options = {
    plugins: {
      legend: { 
        display: false 
      },
      tooltip: {
        callbacks: {
          label: function(context) {
            const label = context.label || '';
            const value = context.parsed || 0;
            const total = context.dataset.data.reduce((acc, val) => acc + val, 0);
            const percentage = ((value / total) * 100).toFixed(1);
            return `${label}: ${value.toLocaleString()} (${percentage}%)`;
          }
        }
      },
      maintainAspectRatio: true,
      responsive: true
    }
  };
  
  return (
    <div style={{width: '90%', maxWidth: 400, minWidth: 120}}>
      <Pie data={data} options={options} style={{width: '100%', height: 'auto'}} />
    </div>
  );
}

export default Tokenomics;