import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';

function About() {
  useEffect(() => {
    document.title = "Token Website | $LVN Token | About";
  }, []);

  return (
    <div className="main-layout page-content about-page">
      <main>
        {/* ===== HEADER ===== */}
        <div className="header-wrapper">
          <h1 className="header-title">
            <span className="header-symbol text-purple">$</span>
            <span className="text-teal">LVN</span>
          </h1>
          <span className="header-tagline text-purple">Building Value Through Time</span>
        </div>
        
        {/* ===== CORE CONCEPT SECTION ===== */}
        <div className="paragraph-container">
          <h2 style={{color: 'var(--color-teal)', marginBottom: '1rem'}}>The Concept</h2>
          <p>
            <span className="text-teal">$</span><span className="text-purple">LVN</span> is a deflationary token built on the Polygon Mainnet that embraces Bitcoin's most powerful innovation: the halving model. With a fixed supply of 8,008,135 tokens and a release schedule spanning decades, we're designed for those who understand that <span className="text-teal">true value appreciates with time</span>.
          </p>
          
          <h2 style={{color: 'var(--color-teal)', marginTop: '2rem', marginBottom: '1rem'}}>Our Philosophy</h2>
          <p>
            In a market flooded with short-term projects, <span className="text-teal">$</span><span className="text-purple">LVN</span> stands for something different. We believe in:
          </p>
          <ul style={{listStyle: 'disc', paddingLeft: '2rem', marginBottom: '1rem'}}>
            <li><span className="text-teal">Transparency</span> — All wallets publicly visible, all allocations openly declared</li>
            <li><span className="text-purple">Predictability</span> — No surprise emissions, no unexpected inflation</li>
            <li><span className="text-teal">Community Growth</span> — Building value through genuine adoption and utility</li>
            <li><span className="text-purple">Long-term Vision</span> — Measuring success in years, not days or weeks</li>
          </ul>
          
          <h2 style={{color: 'var(--color-teal)', marginTop: '2rem', marginBottom: '1rem'}}>What Makes Us Different</h2>
          <p>
            <span className="text-teal">$</span><span className="text-purple">LVN</span> isn't chasing hype cycles or promising overnight riches. We're building a solid foundation with:
          </p>
          <div style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'space-between', margin: '1.5rem 0'}}>
            <div style={{flex: '1 1 280px', margin: '0.5rem', padding: '1rem', borderLeft: '2px solid var(--color-purple)', background: 'rgba(175, 0, 255, 0.05)'}}>
              <h3 style={{color: 'var(--color-teal)', marginBottom: '0.5rem'}}>Bitcoin-Inspired Supply</h3>
              <p>A fixed cap with predictable, transparent halvings every 4 years — ensuring scarcity and long-term value preservation.</p>
            </div>
            <div style={{flex: '1 1 280px', margin: '0.5rem', padding: '1rem', borderLeft: '2px solid var(--color-teal)', background: 'rgba(108, 255, 230, 0.05)'}}>
              <h3 style={{color: 'var(--color-purple)', marginBottom: '0.5rem'}}>Sustainable Tokenomics</h3>
              <p>No artificial pumps, no predatory mechanics, no hidden fees — just sound economic principles designed to reward patience.</p>
            </div>
          </div>
        </div>
        
        {/* ===== VISION SECTION ===== */}
        <div className="paragraph-container" style={{marginTop: '1rem'}}>
          <h2 style={{color: 'var(--color-teal)', marginBottom: '1rem'}}>Our Vision</h2>
          <p>
            We're building <span className="text-teal">$</span><span className="text-purple">LVN</span> for those who see beyond market cycles — for builders, hodlers, and visionaries who understand that meaningful growth takes time. Join us in creating something that's built to last, not just to pump.
          </p>
        </div>
        
        {/* ===== BACK LINK ===== */}
        <div className="back-link-container">
          <Link to="/" className="back-link">← Back to Home</Link>
        </div>
        <div className="bottom-divider"></div>
      </main>
    </div>
  );
}

export default About;