import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import AirdropCountdown from '../airdropcountdown'; // Import as a React component

function Airdrop() {
  useEffect(() => {
    document.title = "Token Website | $LVN Token | Airdrop";
  }, []);

  return (
    <div className="main-layout page-content airdrop-page">
    <AirdropCountdown />

      {/* MAIN CONTENT */}
      <main className="page-content">
        {/* ===== HEADER ===== */}
        <div className="header-wrapper">
          <h1 className="header-title">
            <span className="header-symbol text-purple">$</span>
            <span className="text-teal">LVN</span>
          </h1>
          <span className="header-tagline text-purple">Airdrop Protocol</span>
        </div>
        {/* ===== PARAGRAPH ===== */}
        <div className="paragraph-container">
          <p>
            Information about the <span className="text-teal">$</span><span className="text-purple">LVN</span> Airdrop Protocol will be available soon.
          </p>
        </div>
        {/* ===== BACK LINK ===== */}
        <div className="back-link-container">
          <Link to="/" className="back-link">← Back to Home</Link>
        </div>
        <div className="bottom-divider"></div>
      </main>
    </div>
  );
}

export default Airdrop;
