import React, { useEffect, useState } from "react";

const AIRDROP_START = new Date("2025-08-03T06:31:48Z"); // Airdrop decay start time
const QUARTER_MS = 91 * 24 * 60 * 60 * 1000; // 90 days in ms
const MAX_QUARTERS = 112;

function AirdropCountdown() {
  const [phase, setPhase] = useState("pre"); // "pre" or "airdrop"
  const [currentQuarter, setCurrentQuarter] = useState(0);
  const [timeLeft, setTimeLeft] = useState({});

  useEffect(() => {
    const updateCountdown = () => {
      const now = new Date();

      // Before airdrop schedule starts
      if (now < AIRDROP_START) {
        const diff = AIRDROP_START - now;
        setPhase("pre");
        setTimeLeft(getTimeParts(diff));
        return;
      }

      // After airdrop schedule starts
      setPhase("airdrop");
      const elapsed = now - AIRDROP_START;
      const quarterNumber = Math.floor(elapsed / QUARTER_MS);
      if (quarterNumber >= MAX_QUARTERS) {
        setTimeLeft({ finished: true });
        return;
      }
      setCurrentQuarter(quarterNumber);

      // Time until next airdrop
      const nextDrop = new Date(AIRDROP_START.getTime() + (quarterNumber + 1) * QUARTER_MS);
      const diff = nextDrop - now;
      setTimeLeft(getTimeParts(diff));
    };

    const timer = setInterval(updateCountdown, 1000);
    updateCountdown();
    return () => clearInterval(timer);
  }, []);

  function getTimeParts(diff) {
    if (diff <= 0) return { finished: true };
    const years = Math.floor(diff / (1000 * 60 * 60 * 24 * 365.25));
    diff -= years * 1000 * 60 * 60 * 24 * 365.25;
    const weeks = Math.floor(diff / (1000 * 60 * 60 * 24 * 7));
    diff -= weeks * 1000 * 60 * 60 * 24 * 7;
    const days = Math.floor(diff / (1000 * 60 * 60 * 24));
    diff -= days * 1000 * 60 * 60 * 24;
    const hours = Math.floor((diff / (1000 * 60 * 60)) % 24);
    const minutes = Math.floor((diff / (1000 * 60)) % 60);
    const seconds = Math.floor((diff / 1000) % 60);
    return { years, weeks, days, hours, minutes, seconds };
  }

  function renderTimeLeft(time) {
    const parts = [];
    if (time.years) parts.push(`${time.years}y`);
    if (time.weeks) parts.push(`${time.weeks}w`);
    if (time.days) parts.push(`${time.days}d`);
    if (time.hours) parts.push(`${time.hours}h`);
    if (time.minutes) parts.push(`${time.minutes}m`);
    if (time.seconds !== undefined) parts.push(`${time.seconds}s`);
    return parts.join(" ");
  }

  if (timeLeft.finished) return <div>All airdrops completed.</div>;

  return (
    <div style={{ fontFamily: "monospace", margin: "1em 0" }}>
      {phase === "pre" ? (
        <>
          Airdrop schedule begins in: {renderTimeLeft(timeLeft)}
        </>
      ) : (
        <>
          Next airdrop in: {renderTimeLeft(timeLeft)}
          <br />
          Quarter: {currentQuarter + 1} / {MAX_QUARTERS}
        </>
      )}
    </div>
  );
}

export default AirdropCountdown;