import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

function Home() {
  const navigate = useNavigate();

  useEffect(() => {
    document.title = "Token Website | $LVN Token";
  }, []);

  return (
    <div className="main-layout">
      {/* ===== HEADER ===== */}
      <div className="header-wrapper">
        <h1 className="header-title">
          <span className="header-symbol text-purple">$</span><span className="text-teal">LVN</span>
        </h1>
        <span className="header-tagline text-purple">Built for Long-Term Growth.</span>
      </div>

      {/* ===== HEARTBEAT ===== */}
      <div className="heartbeat-container">
        <svg className="heartbeat-svg" width="100%" viewBox="0 0 600 40">
          <path
            id="heartbeat-path"
            d="M0 20 L60 20 L80 10 L100 30 L120 20 L200 20 L220 15 L240 20 L300 20 L320 5 L340 35 L360 20 L440 20 L460 18 L480 20 L540 20 L560 10 L580 30 L600 20"
            fill="none"
          />
        </svg>
      </div>

      {/* ===== ACTION BUTTONS ===== */}
      <div className="action-btn-group" style={{ justifyContent: 'center', width: '100%', display: 'flex' }}>
        <button
          className="button button-primary"
          style={{ fontSize: '1.1rem', padding: '0.7rem 0', borderRadius: '1.2rem', minWidth: 0, boxShadow: '0 0 8px #6cffe6', width: '60%', maxWidth: 400, margin: '0 auto', display: 'block' }}
          onClick={() => navigate("/liquidity")}
        >
          Buy on DEX
        </button>
      </div>

      {/* ===== PARAGRAPH ===== */}
      <div className="bottom-divider"></div>
    </div>
  );
}

export default Home;
