import React, { useState } from 'react';
import { HashRouter as Router, Routes, Route, Link } from 'react-router-dom';
import './App.css';
import './hamburger.css';
import Home from './pages/Home';
import About from './pages/About';
import Tokenomics from './pages/Tokenomics';
import Airdrop from './pages/Airdrop';
import Liquidity from './pages/Liquidity';
import Staking from './pages/Staking';
import Links from './pages/Links';

function App() {
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <Router>
      <div className="App">
        {/* ===================
           NAVIGATION BAR
        ===================== */}
  <nav className="navbar">
    <button
      className={`menu-icon${menuOpen ? ' menu-icon--open' : ''}`}
      aria-label="Toggle menu"
      aria-expanded={menuOpen}
      onClick={() => setMenuOpen(o => !o)}
    >
      <span className="menu-icon-bar"></span>
      <span className="menu-icon-bar"></span>
      <span className="menu-icon-bar"></span>
    </button>
    <ul className={`navbar-links${menuOpen ? ' navbar-links--open' : ''}`}>
        <li><Link to="/" className="navbar-link" onClick={() => setMenuOpen(false)}>Home</Link></li>
        <li><Link to="/about" className="navbar-link" onClick={() => setMenuOpen(false)}>About</Link></li>
        <li><Link to="/tokenomics" className="navbar-link" onClick={() => setMenuOpen(false)}>Tokenomics</Link></li>
        <li><Link to="/airdrop" className="navbar-link" onClick={() => setMenuOpen(false)}>Airdrop</Link></li>
        <li><Link to="/liquidity" className="navbar-link" onClick={() => setMenuOpen(false)}>Market</Link></li>
        <li><Link to="/staking" className="navbar-link" onClick={() => setMenuOpen(false)}>Staking</Link></li>
        <li><Link to="/links" className="navbar-link" onClick={() => setMenuOpen(false)}>Links</Link></li>
    </ul>
  </nav>
  <div className="navbar-border"></div>
  <Routes>
    <Route path="/" element={<Home />} />
    <Route path="/about" element={<About />} />
    <Route path="/tokenomics" element={<Tokenomics />} />
    <Route path="/airdrop" element={<Airdrop />} />
    <Route path="/liquidity" element={<Liquidity />} />
    <Route path="/staking" element={<Staking />} />
    <Route path="/links" element={<Links />} />
  </Routes>
  {/* ===================
       SCRIPTS
  ===================== */}
  {/* <script src="hamburgerMenu.js"></script> */}
    </div>
    </Router>
  );
}

export default App;
