import React, { useEffect, useState } from "react";

const halvingStartTime = new Date("2025-07-31T02:08:56Z"); // Your vesting schedule start
const halvingIntervalMs = 4 * 365.25 * 24 * 60 * 60 * 1000; // 4 years
const totalHalvings = 31;

function HalvingCountdown() {
  const [phase, setPhase] = useState("pre"); // "pre" or "halving"
  const [currentHalving, setCurrentHalving] = useState(0);
  const [timeLeft, setTimeLeft] = useState({});

  useEffect(() => {
    const updateCountdown = () => {
      const now = new Date();

      // Before vesting schedule starts
      if (now < halvingStartTime) {
        const diff = halvingStartTime - now;
        setPhase("pre");
        setTimeLeft(getTimeParts(diff));
        return;
      }

      // After vesting schedule starts
      setPhase("halving");
      // How many halvings have passed since start?
      const elapsed = now - halvingStartTime;
      const halvingNumber = Math.floor(elapsed / halvingIntervalMs);
      if (halvingNumber >= totalHalvings) {
        setTimeLeft({ finished: true });
        return;
      }
      setCurrentHalving(halvingNumber);

      // Time until next halving
      const nextUnlock = new Date(halvingStartTime.getTime() + (halvingNumber + 1) * halvingIntervalMs);
      const diff = nextUnlock - now;
      setTimeLeft(getTimeParts(diff));
    };

    const timer = setInterval(updateCountdown, 1000);
    updateCountdown();
    return () => clearInterval(timer);
  }, []);

  function getTimeParts(diff) {
    if (diff <= 0) return { finished: true };
    const years = Math.floor(diff / (1000 * 60 * 60 * 24 * 365.25));
    diff -= years * 1000 * 60 * 60 * 24 * 365.25;
    const weeks = Math.floor(diff / (1000 * 60 * 60 * 24 * 7));
    diff -= weeks * 1000 * 60 * 60 * 24 * 7;
    const days = Math.floor(diff / (1000 * 60 * 60 * 24));
    diff -= days * 1000 * 60 * 60 * 24;
    const hours = Math.floor((diff / (1000 * 60 * 60)) % 24);
    const minutes = Math.floor((diff / (1000 * 60)) % 60);
    const seconds = Math.floor((diff / 1000) % 60);
    return { years, weeks, days, hours, minutes, seconds };
  }

  function renderTimeLeft(time) {
    const parts = [];
    if (time.years) parts.push(`${time.years}y`);
    if (time.weeks) parts.push(`${time.weeks}w`);
    if (time.days) parts.push(`${time.days}d`);
    if (time.hours) parts.push(`${time.hours}h`);
    if (time.minutes) parts.push(`${time.minutes}m`);
    if (time.seconds !== undefined) parts.push(`${time.seconds}s`);
    return parts.join(" ");
  }

  if (timeLeft.finished) return <div>All tokens unlocked.</div>;

  return (
    <div style={{ fontFamily: "monospace", margin: "1em 0" }}>
      {phase === "pre" ? (
        <>
          Halving schedule begins in: {renderTimeLeft(timeLeft)}
        </>
      ) : (
        <>
          Current Halving #{currentHalving} | Next halving in: {renderTimeLeft(timeLeft)}
        </>
      )}
    </div>
  );
}

export default HalvingCountdown;